.dashboard-layout {
  display: flex;
  font-family: 'Segoe UI', sans-serif;
  height: 100vh;
}

/* Sidebar */
.sidebar {
  width: 240px;
  background-color: #1e1e2f;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.sidebar-logo {
  font-size: 32px;
  text-align: center;
  margin-bottom: 30px;
}

.sidebar nav ul {
  list-style: none;
  padding: 0;
}

.sidebar nav ul li {
  padding: 12px 0;
  cursor: pointer;
  border-left: 4px solid transparent;
  transition: all 0.3s ease;
}

.sidebar nav ul li:hover,
.sidebar nav ul li.active {
  background-color: #292942;
  border-left: 4px solid #40e0d0;
}

.logout-btn {
  margin-top: auto;
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 4px;
}

/* Main panel */
.main-content {
  flex: 1;
  padding: 20px 40px;
  overflow-y: auto;
}

.topbar {
  padding: 20px 0;
  border-bottom: 1px solid #ccc;
}

.widgets {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.card {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  flex: 1;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.dashboard-body {
  margin-top: 30px;
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.dashboard-content {
  display: flex;
  gap: 2rem;
  margin-top: 2rem;
}

.notifications, .recent-payments {
  background: #fff;
  border-radius: 10px;
  padding: 1.5rem;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.05);
  flex: 1;
}

.recent-payments table {
  width: 100%;
  border-collapse: collapse;
}

.recent-payments th, .recent-payments td {
  padding: 0.5rem;
  border-bottom: 1px solid #ddd;
}
.dashboard-layout {
  background-image: url('../assets/bg-login.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.sidebar-logo {
  text-align: center;
  margin-bottom: 20px;
}

.logo-img {
  width: 120px;
  height: auto;
  border-radius: 8px;
}
