.login-wrapper {
  background: url('../assets/bg-login.jpg') no-repeat center center fixed;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Helvetica Neue', sans-serif;
}

.login-box {
  background-color: rgba(255, 255, 255, 0.95);
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  width: 360px;
  text-align: center;
}

.logo {
  width: 150px;
  margin-bottom: 20px;
}

.login-box h2 {
  font-size: 28px;
  margin-bottom: 10px;
  color: #222;
}

.subtitle {
  font-size: 14px;
  margin-bottom: 25px;
}

.subtitle a {
  color: #444;
  text-decoration: underline;
}

form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

input {
  padding: 12px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 12px;
  background-color: #222;
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
}

button:hover {
  background-color: #444;
}

.forgot {
  font-size: 12px;
  text-align: right;
  margin-top: -10px;
  margin-bottom: 10px;
  display: block;
  color: #666;
  text-decoration: underline;
}

.divider {
  margin: 20px 0 10px;
  font-size: 13px;
  color: #444;
}

.social-login img {
  width: 40px;
  margin: 0 10px;
  cursor: pointer;
}
